// Background

// Background images
.bg-holder {
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
    position: relative;

    .container {
        position: relative;
        z-index: 3;
    }
}

// Scrim & overlays
%bg-scrim {
    content: '';
    height: 80%;
    left: 0;
    position: absolute;
    width: 100%;
    z-index: 1;
}

[data-overlay] {
    &:before {
        content: '';
        display: block;
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
        z-index: 1;
    }
}

[data-scrim-bottom] {
    &:before {
        @extend %bg-scrim;
        background: linear-gradient(to bottom, rgba(0, 0, 0, 0) 0%, rgba(37, 37, 37, 1) 100%);
        bottom: 0;
    }
}

[data-scrim-middle] {
    &:before {
        @extend %bg-scrim;
        background: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(37, 37, 37, 1) 45%, rgba(37, 37, 37, 1) 55%, rgba(0, 0, 0, 0) 100%);
        height: 100%;
        top: 50%;
        transform: translateY(-50%);
    }
}

[data-scrim-top] {
    &:before {
        @extend %bg-scrim;
        background: linear-gradient(to bottom, rgba(37, 37, 37, 1) 0%, rgba(0, 0, 0, 0) 100%);
        top: 0;
    }
}

@for $i from 1 through 9 {
    [data-overlay="#{$i}"] {
        &:before {
            background: rgba(37, 37, 37, calc($i / 10));
        }
    }

    [data-scrim-bottom="#{$i}"],
    [data-scrim-middle="#{$i}"],
    [data-scrim-top="#{$i}"] {
        &:before {
            opacity: calc($i / 10);
        }
    }
}