// Base

a {
    text-decoration: none;
}

// Buttons
button {
    background: transparent;
    border: 0;
}

.btn {
    background: $white;
    border: 2px solid $dark;
    color: $dark;
    font-weight: bold;
    letter-spacing: 0.03em;
    padding: 10px 22px;
    text-transform: uppercase;

    &:not(.border-0) {
        &:hover {
            background: $dark;
            color: $white;
        }
    }
}

// Clipping path images 
.clip-path {
    &::before {
        background-position: center center;
        background-repeat: no-repeat;
        background-size: cover;
        content: '';
        height: 100%;
        left: 0;
        position: absolute;
        top: 0;
        width: 100%;
    }

    &.cp-the-couple-hero {
        &::before {
            background-image: url("../img/clip-path-the-couple-hero.png");
        }
    }

    &.cp-the-couple-1 {
        &::before {
            background-image: url("../img/clip-path-the-couple-1.png");
        }
    }

    &.cp-the-couple-2 {
        &::before {
            background-image: url("../img/clip-path-the-couple-2.png");
        }
    }

    &.cp-love-story {
        &:before {
            background-image: url("../img/clip-path-love-story.png");
        }
    }

    &.cp-instagram {
        &:before {
            background-image: url("../img/clip-path-instagram.png");
        }
    }

    &.cp-the-party {
        &:before {
            background-image: url("../img/clip-path-the-party.png");
        }
    }
}

// Fonts
.font-base {
    font-family: $font-family-base !important;
}

.font-alt {
    font-family: $font-family-alt !important;
}

// Form
input,
select,
textarea {
    box-shadow: none;
    outline: none;
}

// Icons
.icon-svg {
    display: block;

    img {
        height: 44px;
        width: 44px;
    }

    &-lg {
        img {
            height: 62px;
            width: 62px;
        }
    }
}

// Position
.fixed-top {
    left: 0;
    right: 0;
    position: fixed;
    top: 0;
    z-index: $zindex-fixed;
}

// Sections
section {
    padding-bottom: 70px;
    padding-top: 70px;
}

.section-divider {
    display: block;
    margin: 0 auto;
    height: 37px;
    width: 150px;

    &.divider-primary {
        background-image: url("../img/section-divider-primary.svg");
    }

    &.divider-secondary {
        background-image: url("../img/section-divider-secondary.svg");
    }

    &.divider-secondary-2 {
        background-image: url("../img/section-divider-secondary-2.svg");
    }
}

// Separators
.sep-line {
    border-top: 4px solid;
    opacity: 1;
    width: 30px;
}

// Spacer
.spacer {
    height: $navbar-height;
}

@include media-breakpoint-up(lg) {

    // Scrollbar custom
    ::-webkit-scrollbar {
        width: 12px;

        &-track {
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
            -moz-box-shadow: inset 0 0 6px rgba(0, 0, 0, .3);
            box-shadow: inset 0 0 6px rgba(0, 0, 0, .3)
        }

        &-thumb {
            background: rgba(210, 210, 210, .8);
            -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, .5);
            -moz-box-shadow: inset 0 0 6px rgba(0, 0, 0, .5);
            box-shadow: inset 0 0 6px rgba(0, 0, 0, .5)
        }
    }

    // Sections
    section {
        padding-bottom: 120px;
        padding-top: 120px;
    }
}