// Carousel

.carousel-indicators {
    margin: 0;
    position: static;

    [data-bs-target] {
        background-image: url("../img/carousel-indicator.svg");
        background-size: cover;
        border: 0;
        cursor: pointer;
        height: 32px;
        opacity: 1;
        width: 64px;

        &.active {
            background-image: url("../img/carousel-indicator-active.svg");
        }
    }
}